.tweet {
  position: relative;
  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    h4 {
      font-size: 1.4rem;
      margin: 0;
      text-transform: uppercase;
      font-weight: bold;
    }
    p {
      text-align: justify;
      font-size: 1.2rem;
    }
    svg {
      &:hover {
        cursor: pointer;
        fill: red;
      }
    }
  }

  &__date-add-tweet {
    position: absolute;
    bottom: 5px;
    right: 5px;
    font-size: 0.8rem;
    color: #7e5555;
  }
}
