.list-tweets-empty {
  margin-top: 150px !important;
  text-align: center;
  color: white;
}

.list-tweets {
  clear: both;
  display: grid;
  margin-top: 150px !important;
  grid-column-gap: 10px;
  grid-row-gap: 10px;
  grid-auto-rows: minmax(100px, auto);
}

@media (min-width: 720px) {
  .list-tweets {
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 10px;
    grid-row-gap: 10px;
  }
}
